import React, { Component } from 'react';
import Map from './Map';
import Card from './Card';
import IntroPopup from './IntroPopup';
import { FILTER_CATEGORIES } from '../constants/FilterConstants';

// @NOTE: MAKE SURE ARRAY IS [LONGITUDE, LATITUDE]
const defaultCenter = [-108, 38.5];
const defaultBounds = [
  [-180, -85], //southwest
  [180, 85] //northeast
]
const defaultZoom = 3.5;

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      pointCoords: [],
      points: {},
      stories: this.props.stories
    }
    // mapboxgl.accessToken = this.props.mapboxAccessToken;
    mapboxgl.accessToken = "pk.eyJ1Ijoia2FsaW1hciIsImEiOiJjajdhdmNtMjkwbGZlMzJyc2RvNmhjZXd3In0.tBIY2rRDHYt1VYeGTOH98g"
  }

  componentDidMount() {
    const points = this.getPointsFromStories(this.state.stories);
    this.setState({ points: points });
    this.map = new mapboxgl.Map({
      container: this.mapContainer,
      // style: this.props.mapboxStyle,
      style: "mapbox://styles/mapbox/streets-v11",
      center: defaultCenter,
      zoom: defaultZoom,
      maxBounds: defaultBounds
    });
    this.map.on('load', () => {
      this.updateMarkers();
      this.addHomeButton();
    });

    this.map.addControl(new mapboxgl.NavigationControl());
  }

  // componentDidUpdate() {
  //   // [...document.querySelectorAll('.marker')].forEach(function (marker) {
  //   //   marker.remove();
  //   // });

  //   // [...document.querySelectorAll('.mapboxgl-popup')].forEach(function (popup) {
  //   //   popup.remove();
  //   // })

  //   this.updateMarkers();

  //   // if (this.props.pointCoords.length > 0) {
  //   //   if (this.map) {
  //   //     this.map.flyTo({ center: this.props.pointCoords, zoom: 14 });
  //   //   }
  //   //   return;
  //   // }

  //   if (this.map) {
  //     this.map.flyTo({ center: defaultCenter, zoom: defaultZoom })
  //   }
  // }

  createHomeButton() {
    const homeButton = document.createElement('button');
    homeButton.setAttribute('aria-label', 'Map Home');
    homeButton.setAttribute('type', 'button');
    homeButton.setAttribute('class', 'home-icon');
    return homeButton;
  }

  addHomeButton() {
    const homeButton = this.createHomeButton();
    const navControl = document.getElementsByClassName('mapboxgl-ctrl-zoom-in')[0];
    if (navControl) {
      navControl.parentNode.insertBefore(homeButton, navControl);
    }
    homeButton.addEventListener('click', () => {
      this.resetMapToCenter();
    });
  }

  resetMapToCenter() {
    this.map.flyTo({
      center: defaultCenter,
      zoom: defaultZoom,
      maxBounds: defaultBounds
    });
  }

  updateMarkers() {
    this.state.points.features.forEach(marker => {
      (marker.properties);
      // create a HTML element for each feature
      var el = document.createElement('div');
      el.className = 'marker';
      el.id = 'storypoint' + marker.id;
      var popup = new mapboxgl
        .Popup({ offset: 15 })
        .setHTML('<h1>' + marker.properties.title + '</h1>' + '<h2>' + marker.properties.region + '</h2>')
      // make a marker for each feature and add to the map
      new mapboxgl.Marker(el)
        .setLngLat(marker.geometry.coordinates)
        .setPopup(popup) // sets a popup on this marker
        .addTo(this.map);

      el.addEventListener('click', () => {
        this.showMapPointStories(marker.properties.stories);
        this.map.panTo(marker.geometry.coordinates);
      });

      popup.on('close', this.props.clearFilteredStories);
    });

  }

  getPointsFromStories = stories => {
    const points = stories.map(story => story.point);
    const pointObj = {};
    pointObj['features'] = points;
    return pointObj;
  }

  filterMap = () => {
    // Build Filter Map for Dropdowns
    // {category name: array of items}
    let filterMap = {};
    FILTER_CATEGORIES.map(category => {
      switch (category) {
        case FILTER_CATEGORIES[0]: {
          // first category: Region
          const regionSet = new Set(this.props.stories.map(story => {
            return story.point.properties.region
          }));
          filterMap[category] = Array.from(regionSet).sort();
          break;
        }
        case FILTER_CATEGORIES[1]: {
          // second category: Type of Place
          const typeOfPlaceSet = new Set(this.props.stories.map(story => {
            return story.place.type_of_place;
          }));
          filterMap[category] = Array.from(typeOfPlaceSet).sort();
          break;
        }
        case FILTER_CATEGORIES[2]: {
          // third category: Speaker
          const speakerSet = new Set(this.props.stories.map(story => {
            return story.speakers.map(speaker => speaker.name)
          }).flat());
          filterMap[category] = Array.from(speakerSet).sort();
          break;
        }
      }
    });
    return filterMap;
  }

  clearFilteredStories = () => {
    const points = this.getPointsFromStories(this.props.stories);

    this.setState({
      stories: this.props.stories,
      points: points
    });
  }

  handleFilter = (category, item) => {
    let filteredStories = [];
    switch (category) {
      case FILTER_CATEGORIES[0]: {
        // first category: region
        filteredStories = this.props.stories.filter(story => {
          if (story.point.properties.region && story.point.properties.region.toLowerCase() === item.toLowerCase()) {
            return story;
          }
        });
        break;
      }
      case FILTER_CATEGORIES[1]: {
        // second category: type of place
        filteredStories = this.props.stories.filter(story => {
          if (story.place['type_of_place'].toLowerCase() === item.toLowerCase()) {
            return story;
          }
        });
        break;
      }
      case FILTER_CATEGORIES[2]: {
        // third category: speaker name
        filteredStories = this.props.stories.filter(story => {
          if (story.speakers.map(speaker => speaker.name.toLowerCase()).includes(item.toLowerCase())) {
            return story;
          }
        });
        break;
      }
    }
    if (filteredStories) {
      const filteredPoints = this.getPointsFromStories(filteredStories);
      this.setState({ stories: filteredStories, points: filteredPoints, pointCoords: [] });
    }
    (`Filtered Stories of ${category} ${item}:`, filteredStories);
  }

  showMapPointStories = (stories, pointCoords) => {
    let storyPointIds = stories.map(story => story.point_id);
    let filteredStories = [];
    filteredStories = this.props.stories.filter(story => storyPointIds.includes(story.point.id));
    if (filteredStories) {
      this.setState({ stories: filteredStories });
    }
  }

  render() {
    return (
      <div>
        <div ref={
          el => this.mapContainer = el
        }
          className="ts-MainMap" />
        <Card
          stories={this.state.stories}
          categories={FILTER_CATEGORIES}
          filterMap={this.filterMap()}
          handleFilter={this.handleFilter}
          clearFilteredStories={this.clearFilteredStories}
          recenterMap={this.resetMapToCenter}
          onCardClick={this.setPointCoords}
          logo_path={this.props.logo_path}
          user={this.props.user}
        />
        <IntroPopup />
      </div>
    );
  }
}
export default App;



